@import "src/components/common/styles/base/variables";

.form {
  display: flex;
  flex-wrap: wrap;
}

.nameTitle,
.emailTitle,
.subjectTitle,
.messageTitle {
  margin-bottom: 25px;
}

.nameTitle,
.emailTitle {
  flex: 0 0 50%;
  max-width: 50%;

  @media screen and (max-width: $screen-md) {
    flex: 0 0 100%;
    max-width: 100%;
  }
}

.nameTitle {
  padding-right: $container-indent-lg;

  @media screen and (max-width: $screen-md) {
    padding-right: 0;
  }
}

.emailTitle {
  padding-left: $container-indent-lg;

  @media screen and (max-width: $screen-md) {
    padding-left: 0;
  }
}

.message {
  min-height: 120px;

  resize: none;
}

.submitWrapper {
  min-height: 75px;
  width: 160px;
  display: flex;
  align-items: center;
  justify-content: center;
}
